
<template>
  <el-container>
    <el-header>
      <el-menu
          :default-active="activeIndex"
          class="el-menu-demo"
          mode="horizontal"
      >
        <el-menu-item index="1" @click="changeCharts('pi')">饼图</el-menu-item>
        <el-menu-item index="2" @click="changeCharts('line')">折线图</el-menu-item>
        <el-menu-item index="3" @click="changeCharts('map')">地理图</el-menu-item>
      </el-menu>
    </el-header>
    <el-main>

      <br/>
      <el-row>
        <el-col :span="5">
          距今时间：
          <el-select id="dateSelection" :disabled="disable_n_day" style="width: 50%" v-model="shown_days" placeholder="请选择数据距今的时间" @change="this.changeNDays">
            <el-option
                v-for="item in date"
                :key="item.value"
                :label="item.label"
                :value="item.value"
            >
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="5">
          数据来源：
          <el-select id="sourceSelection" :disabled="disable_n_day" style="width: 40%" v-model="shown_data" placeholder="请选择数据来源" @change="this.changeDataSource">
            <el-option
                v-for="item in dataSource"
                :key="item.value"
                :label="item.label"
                :value="item.value"
            >
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="5">
          <div id="Countries">
            国家数量：
            <el-input v-model="select_n_countries" :disabled="disable_input" style="width: 40%" type="number" oninput="value=value.replace(/[^\d]/g,'')" placeholder="输入想查看的国家数量" @change="this.changeCountries" />
          </div>
        </el-col>
      </el-row>
      <p> </p>
      <el-row type="flex">
        <el-col :span="24">
          <el-card id="card" style="height: 70vh;">
            <div class="chart-container">
              <div id="main"  style="width: 100%;height: 65vh;margin:0 auto;">
              </div>
            </div>
            <br/>
          </el-card>
        </el-col>
      </el-row>

    </el-main>
  </el-container>
</template>

<script>

export default {
  data(){
    return {
      activeIndex:'1',
      shown_days:"一周",
      shown_data:"JHU",
      chart_state : 'bing',
      select_n_countries:10,
      select_data_source:"JHU",
      select_n_days:7,
      select_fig:"pi",
      disable_input:false,
      disable_n_day:false,
      dataSource:[
        {
          value:'JHU',
          label:'JHU'
        },{
          value:'WHO',
          label:'WHO'
        },
      ],
      basicDate : [{
        value:1,
        label:'昨天'
      },{
        value:3,
        label:'近3天'
      },{
        value:7,
        label:'一周'
      },{
        value:14,
        label:'近14天'
      },{
        value:30,
        label:'近一个月'
      },{
        value:60,
        label:'近两个月'
      },{
        value:365,
        label:'近一年'
      },],
      date:[
        {
          value:1,
          label:'昨天'
        },{
          value:3,
          label:'近3天'
        },{
          value:7,
          label:'一周'
        },{
          value:14,
          label:'近14天'
        },{
          value:30,
          label:'近一个月'
        },{
          value:60,
          label:'近两个月'
        },{
          value:365,
          label:'近一年'
        },
      ],
      mainCharts:"",
      value: ''
    }
  },
  name: 'Chart',
  methods:{

    changeShown_DataSource(str){
      this.select_data_source = str;
      this.shown_data = str;
    },
    changeShown_N_Days(day){
      this.select_n_days = day;
      for (var i=0;i<this.date.length;i++){
        if (this.date[i].value === this.select_n_days){
          this.shown_days = this.date[i].label;
        }
      }
    },
    changeCountries(num){
      this.select_n_countries = parseInt(num)
      this.buildCharts(this.select_fig);
    },
    changeDataSource(str){
      this.changeShown_DataSource(str);
      this.buildCharts(this.select_fig);
    },
    changeNDays(day){
      this.changeShown_N_Days(day);
      this.buildCharts(this.select_fig);
    },
    changeCharts(str){
      this.select_fig = str
      if (str === "pi"){
        this.activeIndex = '1';
        this.date = JSON.parse(JSON.stringify(this.basicDate));
        this.dataSource = [
          {
            value:'JHU',
            label:'JHU'
          },{
            value:'WHO',
            label:'WHO'
          },
        ];
        this.disable_input = false;
        this.disable_n_day = false;
        console.log(this.date)
        this.changeShown_N_Days(this.date[2].value);
        this.changeShown_DataSource(this.dataSource[0].value);
      }else if (str === "line"){
        this.activeIndex = '2';
        this.date = [{
          value: '2020' ,
          label: '2020年'
        }];
        this.dataSource = [
          {
            value:'JHU',
            label:'JHU'
          },{
            value:'WHO',
            label:'WHO'
          },
        ];
        this.disable_input = false;
        this.disable_n_day = false;
        this.changeShown_N_Days(this.date[0].value);
        this.changeShown_DataSource(this.dataSource[0].value);
      }else {
        this.activeIndex = '3';
        this.date = [{
          value:1,
          label:'昨天'
        }];
        this.dataSource = [{
          value:'腾讯',
          label:'腾讯'
        }];
        this.disable_input = true;
        this.disable_n_day = true;
        this.changeShown_N_Days(this.date[0].value);
        this.changeShown_DataSource(this.dataSource[0].value);
      }
      this.buildCharts(str);
    },
    buildCharts(figType){
      this.$forceUpdate();
      if(figType === "pi"){
        this.piCharts();
      }else if(figType === 'line'){
        this.lineCharts();
      }else {
        this.mapChart();
      }
    },
    lineCharts(){
      this.$axios({
        method:'get',
        url:'api/lineRace',
        params:{
          source:this.select_data_source,
          n:this.select_n_countries
        }
      }).then((response) =>{          //这里使用了ES6的语法
        response = response.data
        this.$echarts.init(document.getElementById('main')).dispose();
        this.mainCharts = this.$echarts.init(document.getElementById('main'));
        let c = response.legendData;
        const countries = c;
        const datasetWithFilters = [];
        const seriesList = [];
        this.$echarts.util.each(countries, function (country) {
          var datasetId = 'dataset_' + country;
          datasetWithFilters.push({
            id: datasetId,
            fromDatasetId: 'dataset_raw',
            transform: {
              type: 'filter',
              config: {
                and: [
                  { dimension: 'Country', '=': country }
                ]
              }
            }
          });
          seriesList.push({
            type: 'line',
            datasetId: datasetId,
            showSymbol: false,
            name: country,
            endLabel: {
              show: true,
              formatter: function (params) {
                return params.value[0];
              }
            },
            labelLayout: {
              moveOverlap: 'shiftY'
            },
            emphasis: {
              focus: 'series'
            },
            encode: {
              x: 'Month',
              y: 'Cases',
              label: ['Country', 'Cases'],
              itemName: 'Month',
              tooltip: ['Cases']
            }
          });
        });

        var option = {
          animationDuration: 5000,
          animation: 'auto',
          animationDurationUpdate: 500,
          animationEasing: 'cubicInOut',
          animationEasingUpdate: 'cubicInOut',
          animationThreshold: 2000,
          progressiveThreshold: 3000,
          progressive: 400,
          hoverLayerThreshold: 3000,
          useUTC: false,
          stateAnimation:{
            duration:300,
            easing : 'cubicOut'
          },
          dataset: [
            {
              id: 'dataset_raw',
              source: response.source
            },
            ...datasetWithFilters
          ],
          title: {
            text: '2020年至今全球疫情增长曲线',
            left: 'center'
          },
          tooltip: {
            order: 'valueDesc',
            trigger: 'axis'
          },
          xAxis: {
            type: 'category',
            nameLocation: 'middle'
          },
          yAxis: {
            name: '确诊病例'
          },
          grid: {
            right: 140
          },toolbox: {
            show: true,
            //orient: 'vertical',
            left: 'left',
            top: 'top',
            feature: {
              dataView: {
                readOnly: true
              },
              restore: {},
              saveAsImage: {}
            }
          },
          series: seriesList
        };
        this.mainCharts.setOption(option);
      }).catch((error) =>{
        console.log(error)       //请求失败返回的数据
      })

    },
    piCharts(){
      this.$axios({
        method:'get',
        url:'api/pieChart',
        params:{
          source:this.select_data_source,
          nDays:this.select_n_days,
          n:this.select_n_countries,
        }
      }).then((response) =>{          //这里使用了ES6的语法
        response = response.data
        this.$echarts.init(document.getElementById('main')).dispose();
        this.mainCharts = this.$echarts.init(document.getElementById('main'));
        var option = {
          title: {
            text: this.shown_days+"全球新冠肺炎新增确诊病例",
            subtext: '数据来源：'+this.select_data_source,
            left: 'center'
          },
          tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b} : {c} ({d}%)'
          },

          legend: {
            type: 'scroll',
            orient: 'vertical',
            right: 10,
            top: 20,
            bottom: 20,
            data: response.legendData
          },toolbox: {
            show: true,
            //orient: 'vertical',
            left: 'left',
            top: 'top',
            feature: {
              dataView: {
                readOnly: true
              },
              restore: {},
              saveAsImage: {}
            }
          },
          series: [
            {
              name: '国家名',
              type: 'pie',
              radius: '55%',
              center: ['40%', '50%'],
              color:[
                '#5470c6',
                '#91cc75',
                '#fac858',
                '#ee6666',
                '#73c0de',
                '#3ba272',
                '#fc8452',
                '#9a60b4',
                '#0040c4',
                '#ee2662',
                '#91ac72'],
              data: response.seriesData,
              emphasis: {
                itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        };
        this.mainCharts.setOption(option);
        this.mainCharts.resize();
      }).catch((error) =>{
        console.log(error)       //请求失败返回的数据
      })

    },
    mapChart(){
      this.$axios({
        method:'get',
        url:'/api/mapChart',
      }).then((response) =>{          //这里使用了ES6的语法
        response = response.data
        this.$echarts.init(document.getElementById('main')).dispose();
        this.mainCharts = this.$echarts.init(document.getElementById('main'));
        this.mainCharts.hideLoading();
        this.$echarts.registerMap('China',response.map);
        var option = {
          title: {
            text: '昨日国内新增确诊数',
            subtext: '数据来源：腾讯新闻',
            sublink: 'https://news.qq.com/',
            left: 'right'
          },
          tooltip: {
            trigger: 'item',
            showDelay: 0,
            transitionDuration: 0.2,
            formatter: function (params) {
              const value = (params.value + '').split('.');
              const valueStr = value[0].replace(
                  /(\d{1,3})(?=(?:\d{3})+(?!\d))/g,
                  '$1,'
              );
              return params.seriesName + '<br/>' + params.name + ': ' + valueStr;
            }
          },
          visualMap: {
            left: 'right',
            min: 0,
            max: response.max,
            inRange: {
              color: [
                '#ffffff',
                '#ffffbf',
                '#fee090',
                '#fdae61',
                '#f46d43',
                '#d73027',
                '#a50026'
              ]
            },
            text: ['High', 'Low'],
            calculable: true
          },
          toolbox: {
            show: true,
            //orient: 'vertical',
            left: 'left',
            top: 'top',
            feature: {
              dataView: {
                readOnly: true
              },
              restore: {},
              saveAsImage: {}
            }
          },
          series: [
            {
              name: '现有确诊量',
              type: 'map',
              roam: true,
              map: 'China',
              emphasis: {
                label: {
                  show: true
                }
              },
              data: response.seriesData
            }
          ]
        };
        this.mainCharts.setOption(option);
      }).catch((error) =>{
        console.log(error)
      })

    }
  },

  mounted() {
    window.onresize = () => {
      this.mainCharts.resize();
    }
    this.changeCharts("pi");

  }
}


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin: 40px 0 0;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}


</style>
